import styles from "./index.module.css";
import { Box, Typography, Button } from "@mui/material";
import { TITLE, CONTENT } from "../../../constants/demo/compd";
import pic_one from "../../../assets/CompD.png";
import pic_two from "../../../assets/CompD-table1.png";
import pic_three from "../../../assets/CompD-table2.png";
import axios from "axios";

// getOriAcc()

